<template>
	<el-tabs v-model="activeName" @tab-click="handleClick" class="box">
    <el-tab-pane label="登录" name="first">
		  <el-form :model="loginForm" ref="loginForm" :rules="rules">
		  	<el-form-item prop="username">
			    <el-input v-model="loginForm.username" placeholder="请输入内容" class="mt30">
			    	<template slot="prepend"><el-button type="primary" icon="el-icon-edit"class="uicon"></el-button></template>
			    </el-input>
			  </el-form-item>  
			  <el-form-item prop="pwd">
			    <el-input v-model="loginForm.pwd" placeholder="请输入密码">
			    	<template slot="prepend"><el-button type="primary" icon="el-icon-edit"class="uicon"></el-button></template>
			    </el-input>
			  </el-form-item>  
			  	<el-button @click="submitForm('loginForm')" round type="primary" class="login">登录</el-button>
			  	<el-button type="mini" icon="el-icon-service">QQ登录</el-button>
			</el-form>
    </el-tab-pane>
    <el-tab-pane label="注册" name="second">
    	<el-form :model="regForm":rules="rules" ref="regForm">
    		<el-form-item prop="username">
			    <el-input v-model="regForm.username"placeholder="请输入内容" class="mt30">
			    	<template slot="prepend"><el-button type="primary" icon="el-icon-edit"class="uicon"></el-button></template>
			    </el-input>
			  </el-form-item>  
			    <el-form-item prop="pwd">
			    <el-input v-model="regForm.pwd"placeholder="请输入密码">
			    	<template slot="prepend"><el-button type="primary" icon="el-icon-edit"class="uicon"></el-button></template>
			    </el-input>
			  </el-form-item>  
			    <el-form-item prop="confirmpwd">
			    <el-input v-model="regForm.confirmpwd"placeholder="请再次输入密码">
			    	<template slot="prepend"><el-button type="primary" icon="el-icon-edit"class="uicon"></el-button></template>
			    </el-input>
			   </el-form-item> 
			  	<el-button @click="submitForm('regForm')" round type="success" class="login">注册</el-button>
			</el-form>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
  name: 'Login',
    data() {
    	var validatePass=(rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('请再次输入密码'));
	        } else if (value !== this.regForm.pwd) {
	          callback(new Error('两次输入密码不一致!'));
	        } else {
	          callback();
	        }
        };
      return {
        loginForm: {
          username: 'admin',
          pwd: '123456'
        },
        regForm: {
          username: '',
          pwd: '',
          confirmpwd:''
        },
        activeName: 'first',
        rules: {
          username: [
            { required: true, message: '请输入内容', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          pwd: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          confirmpwd: [
            { validator: validatePass, trigger: 'blur' }
          ],
        }  
      }
    },
    beforeCreate:
    	function(){
    		console.log(1)
    	}
    ,
    methods:{
    	handleClick(tab, event) {
        console.log(tab, event);
     },
     submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
          	var _this=this;
          	if(formName==='loginForm'){
          		this.$http.post( 'api/login',this.loginForm)
		            .then(function(res){
		       			console.log(res)
		       			if(res.data.success){
		       				_this.$store.commit('login',res.data.data);
							_this.$router.push('/Member/User')
		       			}else{
		       				_this.$message.error(res.data.message)
		       			}
		            },function(){
		              alert("error")
		          })
	          	}else{
	          		this.$http.post( 'api/reg',this.regForm)
		            .then(function(res){
		       			console.log(res)
		       			if(res.data.success){
		       				_this.$message.success(res.data.message+'请登录')
		       				_this.activeName='first'
		       				_this.loginForm.username=_this.regForm.username
		       				_this.loginForm.pwd=_this.regForm.pwd
		       			}else{
		       				_this.$message.error(res.data.message)
		       			}
		            },function(){
		              alert("error")
		          })
	          	}
        	} else {
            console.log('error submit!!');
            return false;
          }
        })  
      },
    }
}
</script>

<style scoped>
	.box{ width: 360px;position:  absolute;left: 50%;top: 50%;margin-left: -200px;margin-top: -300px;background: #fff;padding: 20px;border: 1px solid #ddd;border-radius:8px ;}
	.login{width: 100%;font-size: 16px;margin: 20px 0;}
</style>



