<template>
<el-container>
  <el-header>
  	<el-dropdown @command="handleCommand" split-button type="primary"class="fr mt10" >
	  {{$store.state.userdata[0].UserName}}
	  <el-dropdown-menu slot="dropdown">
	    <el-dropdown-item command="LoginOut">{{$store.state.isLogin}}退出</el-dropdown-item>
	    <el-dropdown-item>修改密码</el-dropdown-item>
	  </el-dropdown-menu>
	</el-dropdown>
  </el-header>
  <el-container>
    <el-aside width="200px">
    	<el-menu
      default-active="1-1"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>导航一</span>
        </template>
          	<router-link to="/Member/User"><el-menu-item index="1-1">选项1</el-menu-item></router-link>
          	<router-link to="/Member/Visitor"><el-menu-item index="1-2">选项2</el-menu-item></router-link>
        	<el-menu-item index="1-3">选项3</el-menu-item>
        	<el-submenu index="1-4">
          		<template slot="title">选项4</template>
          		<el-menu-item index="1-4-1">选项1</el-menu-item>
        	</el-submenu>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">导航二</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">导航三</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">导航四</span>
      </el-menu-item>
    </el-menu>
    </el-aside>
    <el-main>
    	<transition name="slide-left">
    		<router-view/>
    	</transition>
    </el-main>
  </el-container>
</el-container>
</template>


<script>
	export default {
  		name: 'Member',
		data() {
		  return {
		  }
		},  
		methods: {
		    handleOpen(key, keyPath) {
		        console.log(key, keyPath);
		    },
		    handleClose(key, keyPath) {
		        console.log(key, keyPath);
		    },
		    handleCommand(command){
		    	if(command=='LoginOut'){
		    		this.$store.commit('loginOut');
		    		this.$router.push({path:'/'})
		    	}
		    },
		    
	    },
    }
</script>

<style>
	.el-header{background: #409EFF;}

</style>


