<template>
<div>
	<el-button type="primary" size="mini" icon="el-icon-edit" @click="add">添加</el-button>
	<el-button type="primary" size="mini" icon="el-icon-delete"@click="allDelete">删除选中</el-button>
	<el-table
    ref="multipleTable"
    :data="tableData3"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="40"
      >
    </el-table-column>
    <el-table-column
      label="日期"
      width="120"
      prop="date">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="90">
    </el-table-column>
    <el-table-column
    	width="380"
      prop="address"
      label="地址"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
    	width="220"
      prop="other"
      label="备注"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="edit"
      label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog :model="form" :title="form.name" :visible="dialogFormVisible">
  <el-form>
    <el-form-item label="日期" :label-width="formLabelWidth">
      <el-input v-model="form.date" auto-complete="off":disabled="true"></el-input>
    </el-form-item>
    <el-form-item label="地址" :label-width="formLabelWidth">
      <el-input type="textarea" v-model="form.address" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="描述" :label-width="formLabelWidth">
      <el-input type="textarea" v-model="form.other" auto-complete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible=false">取 消</el-button>
    <el-button type="primary" @click="editCommit">确 定</el-button>
  </div>
</el-dialog>
<el-dialog :model="AddForm" title="添加用户" :visible="dialogAddForm">
  <el-form>
	<el-form-item label="日期" :label-width="formLabelWidth">
      <el-input v-model="AddForm.date" auto-complete="off":disabled="true"></el-input>
    </el-form-item>
  	<el-form-item label="姓名" :label-width="formLabelWidth">
      <el-input v-model="AddForm.name" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="地址" :label-width="formLabelWidth">
      <el-input type="textarea" v-model="AddForm.address" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="描述" :label-width="formLabelWidth">
      <el-input type="textarea" v-model="AddForm.other" auto-complete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogAddForm=false">取 消</el-button>
    <el-button type="primary" @click="AddFormCommit">确 定</el-button>
  </div>
</el-dialog>
</div>
</template>

<script>
export default {
	name: 'User',
		data() {
		  return {
		  	dialogFormVisible: false,
		  	dialogAddForm: false,
		  	formLabelWidth:'50px',
		  	multipleSelection: [],
		  	tableData3: [{
		  		id:1,
	          date: '2016-05-03',
	          name: '王小虎',
	          address: '上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄',
	          other: '上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄'
	        }, {
	        	id:2,
	          date: '2016-05-02',
	          name: '王小虎',
	          address: '上海市普陀区金沙江路 1518 弄',
	          other: '上海市普陀区金沙江路 1518 弄'
	        }
	        ],
	        form: {
	        	id:'',
	          name: '',
	          date: '',
	          address: '',
	          other: '',
	        },
	       	currentEditID:'',
	       	AddForm:{
	       		id:'',
	          name: '',
	          date: '',
	          address: '',
	          other: '',
	       	}
		  }
		},
		methods:{
			handleEdit(index, row) {
				this.dialogFormVisible=true;
				this.form=JSON.parse(JSON.stringify(row));
				this.currentEditID=index;
		      },
		      
		      handleDelete(index, row) {
		      	this.$confirm(`确认将${row.name}删除？`,'提示')
		      		.then(()=>{
		      			this.tableData3.splice(index,1)
		      		})
		      		.catch(()=>{})
		      },
		      editCommit(){
		      	this.$set(this.tableData3, this.currentEditID, this.form);
		      	this.dialogFormVisible=false;
		      },
		      add(){
		      	this.AddForm.address='';
		      	this.AddForm.other='';
		      	this.AddForm.name='';
		      	this.AddForm.date=new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate();
		      	this.dialogAddForm=true;
		      },
		      AddFormCommit(){
		      	this.tableData3.push(JSON.parse(JSON.stringify(this.AddForm)))
		      	this.dialogAddForm=false;
		      },
		       handleSelectionChange(val) {
        		this.multipleSelection = val;
        		console.log(this.multipleSelection)
     		 },
     		 allDelete(){
     		 	if(this.multipleSelection==''){
     		 		this.$message('请先勾选')
     		 		return false;
     		 	}
     			for (var i = 0; i < this.multipleSelection.length; i++) {
     				var ii= this.tableData3.indexOf(this.multipleSelection[i])
     				this.tableData3.splice(ii,1)
     			}
     		 }
		      
		}
	}	
</script>

<style>
</style>


