import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    isLogin: 0,
    userdata:''
  },
  mutations: {
    loginOut (state) {
    	state.isLogin=0;
    	state.userdata='';
    },
    login(state,n){
    	state.isLogin=1;
    	state.userdata=n;
    }
  }
})
export default store


