<template>
	<el-table
    ref="multipleTable"
    :data="tableData3"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="">
    <el-table-column
      type="selection"
      width="40">
    </el-table-column>
    <el-table-column
      label="日期"
      width="120"
      prop="date">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="90">
    </el-table-column>
    <el-table-column
    	width="380"
      prop="address"
      label="地址"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
    	width="220"
      prop="other"
      label="备注"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="edit"
      label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
	export default {
	name: 'Visitor',
		data() {
		  return {
		  	tableData3: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄',
          other: '上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          other: '上海市普陀区金沙江路 1518 弄'
        },{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          other: '上海市普陀区金沙江路 1518 弄'
        }
        ],
		  }
		}
	}	
</script>

